@mixin padding-classes
@for $i from 1 through 50
    $padding: $i * 1px

    .p#{$i}
        padding: $padding

    .pl#{$i}
        padding-left: $padding

    .pr#{$i}
        padding-right: $padding

    .pt#{$i}
        padding-top: $padding

    .pb#{$i}
        padding-bottom: $padding

    .px#{$i}
        padding-left: $padding
        padding-right: $padding

    .py#{$i}
        padding-top: $padding
        padding-bottom: $padding

@include padding-classes

@mixin margin-classes
@for $i from 1 through 200
    $margin: $i * 1px
    /* margin #{$margin} */
    .m#{$i}
        margin: $margin

    .ml#{$i}
        margin-left: $margin

    .mr#{$i}
        margin-right: $margin

    .mt#{$i}
        margin-top: $margin

    .mb#{$i}
        margin-bottom: $margin

    .mx#{$i}
        margin-left: $margin
        margin-right: $margin

    .my#{$i}
        margin-top: $margin
        margin-bottom: $margin

@include margin-classes
